@import url(https://fonts.googleapis.com/css?family=Montserrat|Nunito|Roboto+Condensed&display=swap);
@import url(https://fonts.googleapis.com/css?family=Oxygen|Raleway|Rubik|Work+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.test {
    border: 2px solid black;
}

.column1 {
    float: left;
    width: 20%;
}
.column2 {
    float: right;
    width: 80%;
}

* {
    box-sizing: border-box;
}

.main {
    height: 200%;
    width: 20%;
    background-color: #f2f3f7;
    padding: 25px;
    position: fixed;
}

.text {
    font-size: 30px;
    font-family: Georgia, serif;
    font-style: normal;
}

.image {
    height: 150px;
    width: 150px;
    border-radius: 2000px;
    margin: 50px 30px 0;
}

.contact {
    padding-left: 15px;
    line-height: 25px;
}

.info {
    font-family: Nunito, sans-serif;
    font-size: 14px;
    line-height: 7px;
    text-decoration: none;
    text-align: center;
}
.end {
    padding-top: 50px;
    text-align: center;
}
.end-text {
    font-size: 15px;
    line-break: strict;
    font-family: Arial, sans-serif;
    color: grey;
}

.cv {
    margin: 20px;
    padding: 10px;
}


.about-main {
    border: 0.5px solid #c9d9e2;
    padding: 2px 15px 10px;
    margin: 20px 10px 10px;
    border-radius: 8px;
    background-color: #e1f2fb;
}
.heading {

}
.heading-2 {
    line-height: 10px;
}
.about-me {
    font-family: Rubik, sans-serif;
    font-size: 32px;
    font-weight: 500;
}
.sub-question {
    font-family: Raleway, sans-serif;
    font-size: 20px;
}
.detail-text {
    font-size: 16px;
    font-family: Rubik, sans-serif;
    text-align: left;
    line-height: 22px;
}

.education-main {
    border: 0.5px solid #c5cdcd;
    padding: 2px 15px 10px;
    margin: 20px 10px 10px;
    border-radius: 8px;
    background-color: #f1f9f9;
}

.education {
    font-family: Rubik, sans-serif;
    font-size: 32px;
    font-weight: 500;
}

.completion {
    line-height: 1px;
    font-size: 14px;
    font-weight: bold;
}

.education-heading-2 {
    line-height: 1px;
}

.sub-question-degree {
    font-family: Lato, sans-serif;
    font-size: 19px;
    line-height: 4px;
}

.education-detail-text {
    font-size: 16px;
    line-height: 24px;
    font-family: Rubik, serif;
    font-weight: 100;
}

.experience-main {
    border: 0.5px solid rgba(228, 50, 52, 0.2);
    padding: 2px 15px 10px;
    margin: 20px 10px 10px;
    border-radius: 8px;
    background-color: rgba(255, 23, 73, 0.06);
}

.experience-heading-2 {
    line-height: 1px;
}

.sub-question-company {
    font-family: Lato, sans-serif;
    font-size: 20px;
    line-height: 4px;
}

.completion-experience {
    line-height: 1px;
    font-size: 14px;
    font-weight: bold;
}

.experience-detail {
}

.experience-detail-text {
    line-height: 12px;
    font-family: Rubik, sans-serif;
}

.skills-main {
    border: 1px solid rgba(179, 179, 150, 0.56);
    padding: 2px 15px 10px;
    margin: 20px 10px 10px;
    border-radius: 8px;
    background-color: rgba(251, 251, 217, 0.26);
}

.skills-heading {
    font-family: Rubik, sans-serif;
    font-size: 32px;
    padding-top: 36px;
    padding-bottom: 10px;
}

.skill-group {
    height: 50px;
    margin-bottom: 8px;
}

.skill-text {
    float: left;
    width: 30%;
    text-align: start;
}

.skill-bar {
    float: left;
    width: 55%;
    margin-top: 20px;
}

.skill-text-main {
    font-size: 18px;
    font-family: Rubik, sans-serif;
}

